<template>
  <div class="row">
      <div class="col-auto" id="profile">
        <div class="wall">
          <img src="../assets/logo.png" alt="">          
        </div>
        <p>Job</p>
      </div>
      <div class="col" id="info">
        <p class="name">Pluto Nimolar</p>
        <health/>
      </div>
  </div>
</template>

<script>
import Health from '@/components/Health'
export default {
  name: 'User',
  components: {Health}
}
</script>

<style lang="scss" scoped>
#profile {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  .wall {
    width: 100px;
    height: 100px;
    position: relative;
    border: 1px solid #000;
    border-radius: 50%;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
  p {
    text-align: center;
    width: 100%;
    padding: 0.2em;
    bottom: 0;
    left: 0;
    position: absolute;
    margin: 0;
    background-color: rgb(3, 54, 129);
    color: #fff;
  }
}

#info {
  .name {
    text-align: left;
  }
}

</style>





