<template>
<div class="item row">
  <div class="col-2 img">
    <img src="../assets/logo.png" alt="">
  </div>
  <div class="col-7 name">name</div>
  <div class="col-3 trade" @click="active = true">More</div>
  <div v-if="active" class="overlay">
    <div class="content">
      <div class="container">
        <div class="row">
          <div class="col">
            <h1>Overlay</h1>
          </div>
        </div>
        <div class="row">
          <div class="col" v-if="!trade">
            <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Voluptatem, odit excepturi! Impedit eveniet sunt ab
              delectus eius amet perferendis a iusto. Explicabo, illum quia nobis laborum delectus ullam at necessitatibus.</p>
          </div>
          <div class="col" v-else>
            <h2>Trading Portal</h2>
            <p>Make your parter scan this qr code to trade this item to them</p>
            <qr-code class="qrcode" text="Text to encode"></qr-code>
          </div>
        </div>
        <div class="row btn-group">
          <button v-if="!trade" @click="trade = true" style="background-color: rgb(3, 54, 129)" class="btn col">Trade</button>
          <button v-else @click="trade = false" style="background-color: rgb(3, 54, 129)" class="btn col">Cancel</button>
          <button style="background-color: rgb(59, 59, 59)" class="btn col" @click="abort">Close</button>
        </div>
      </div>
    </div>
  </div>
</div>

</template>

<script>
import QrCode from 'vue-qrcode-component'
export default {
  name: 'Item',
  components: {QrCode},
  data () {
    return {
      active: false,
      trade: false
    }
  },
  methods: {
    abort () {
      this.active = false
      this.trade = false
    }
  }
}
</script>

<style lang="scss" scoped>
.item {
  overflow: hidden;
  margin: 1em 0;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  img {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px;
  }
  .name {
    padding: 1em 0.2em;
  }
  .trade {
    background-color: rgb(3, 54, 129);
    color: #fff;
    padding: 0.5em;
    border-radius: 0 0.25em 0.25em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: 100vh;
  background: rgba(0,0,0,0.8);
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  .content {
    width: 90vw;
    max-width: 600px;
    background: #fff;
    border-radius: 0.25em;
    position: relative;
    display: block;
  }
  .btn-group {
    margin-bottom: 3px;
    .btn {
      margin: 3px;
      float: left;
      position: relative;
      display: block;
      border: none;
      padding: 1em 2em;
      color: #fff;
      border-radius: 0.25em;
      text-align: center;
    }
  }
}

.qrcode {
  width: 256px;
  margin: 0 auto;
}
</style>



